*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.show {
  display: flex!important;
}
.hide {
  display: none;
}
body{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background: #dee2e6;
}
ol,ul{
    list-style: none;
}
a{
    text-decoration: none;
}
.container{
    width: 1140px;
    margin: 0 auto;
}
/* header section start */
.header > nav > .container{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.header > nav{
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 100;
    padding: 15px;
    background: rgb(0, 153, 255);
}
.logotip-box h1 > a{
    color: #1d3557;
    font-size: 35px;
    font-weight: bold;
    cursor: pointer;
    -webkit-text-fill-color: #1d3557;
    -webkit-text-stroke-width: 2px;
    -webkit-text-stroke-color: #fff;
}
#form{
    position: relative;
}
#form > input{
    padding: 10px;
    width: 500px;
    height: 40px;
    font-size: 17px;
    font-weight: bold;
    background: #f8f9fa;
    border: 1px solid #f8f9fa;
    outline: none;
    color: #777;
    border-radius: 10px;
}
#form > #search-btn{
    position: absolute;
    border: none;
    background: none;
    outline: none;
    font-size: 30px;
    font-weight: bold;
    right: 10px;
    top: 50%;
    transform: translateY(-40%);
}
#form > #search-btn i{
    color: #1d3557;
}
.cart-btn{
    border: none;
    outline: none;
    background: none;
    font-size: 38px;
    font-weight: bold;
    color: #1d3557;
    transition: .5s ease;
}
.cart-btn:hover{
    color: #fff;
}
/* ! offcanvas start */
#offcanvas-content{
    position: fixed;
    top: 0;
    right: 0;
    width: 30%;
    height: 100%;
    transform: translateX(110%);
    padding: 10px 20px;
    background-color: #fff;
    overflow: scroll;
    transition: 1s ease;
    z-index: 150;
}
.close-offcanvas{
    position: absolute;
    top: -2px;
    left: 15px;
    font-size: 40px;
    font-weight: bold;
    color: #144283;
    transition: 1s ease;
}
#offcanvas-content:target{
    transform: translateX(10%);
}
.manzil_canvas{
  margin-top: 70px;
}
.manzil_canvas > li{
  width: 250px;
  height: 300px;
  border-radius: 10px;
  border: 2px solid rgba(204, 205, 206, 0.856);
  background: rgb(252, 252, 252);
  margin-top: 20px;
  padding: 15px;
}
/* ! offcanvas end */
.navigation{
  margin-top: 150px;
}
.navigation > .menu{
    display: flex;
    align-items: center ;
    justify-content: space-evenly;
    margin-top: 50px;
}
.navigation > .menu > .menu-item > .menu-link{
    color: #444;
    font-size: 17px;
    font-weight: 600;
    transition: .5s;
    background:rgb(238, 235, 235);
    padding: 10px;
    border-radius: 10px;
    font-style: italic;
}
.navigation > .menu > .menu-item > .menu-link:hover{
    background: rgb(0, 153, 255);
    padding: 10px;
    color: #fff;
    border-radius: 10px;
}
/* header section end */
/* tabcontent section start */
.tabcontent{
  margin-top: 50px;
  background: linear-gradient(90deg, rgb(98, 0, 255), rgb(0, 60, 255));
}
.hide {
  -webkit-animation-name: fade;
          animation-name: fade;
  -webkit-animation-duration: 2.5s;
          animation-duration: 2.5s;
  -webkit-animation-name: fade;
  -webkit-animation-duration: 2.5s;
  transition: .7s;
}
@-webkit-keyframes fade {
  from {
    opacity: 0.1;
  }
  to {
    opacity: 1;
  }
}
@keyframes fade {
  from {
    opacity: 0.1;
  }
  to {
    opacity: 1;
  }
}
/* tabcontent section end */
/* main start */
.main{
    background:rgb(238, 235, 235);
    background-attachment: fixed;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}
/* main-end */
/* ...... section start */
.installs{
  background: linear-gradient(80deg, rgb(98, 0, 255), rgb(0, 60, 255));
}
.installs > .container > .hide{
  -webkit-animation-name: fade;
  animation-name: fade;
-webkit-animation-duration: 2.5s;
  animation-duration: 2.5s;
-webkit-animation-name: fade;
-webkit-animation-duration: 2.5s;
transition: .7s;
}
@-webkit-keyframes fade {
from {
opacity: 0.1;
}
to {
opacity: 1;
}
}
@keyframes fade {
from {
opacity: 0.1;
}
to {
opacity: 1;
}
}
.instal-section{
    padding: 50px 0;
}
.instal-section > .container > h2{
    font-size: 25px;
    color: #1d3557;
    font-weight: bold;
}
.install-list{
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.instal-item{
    position: relative;
    width: 250px;
    height: 300px;
    border-radius: 20px;
}
.instal-item > .clip{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: .5s ease-in;
    border-radius: 10px;
   
}
.instal-item > .clip.clip1{
    background:url('../imgs/hover-img1.jpg');
    background-size: cover;
    clip-path: polygon(0 0, 70% 0, 30% 100%, 0 100%);
}
.instal-item > .clip.clip2{
    background:url('../imgs/hover-img5.jpg');
    background-size: cover;
    clip-path: polygon(70% 0, 100% 0, 100% 100%, 30% 100%);
}
.instal-item > .clip.clip3{
    background:url('../imgs/hover-img2.jpg');
    background-size: cover;
    clip-path: polygon(0 0, 70% 0, 30% 100%, 0 100%);
}
.instal-item > .clip.clip4{
    background:url('../imgs/hover-img6.jpg');
    background-size: cover;
    clip-path: polygon(70% 0, 100% 0, 100% 100%, 30% 100%);
}
.instal-item > .clip.clip5{
    background:url('../imgs/hover-img3.jpg');
    background-size: cover;
    clip-path: polygon(0 0, 70% 0, 30% 100%, 0 100%);
}
.instal-item > .clip.clip6{
    background:url('../imgs/hover-img7.jpg');
    background-size: cover;;
    clip-path: polygon(70% 0, 100% 0, 100% 100%, 30% 100%);
}
.instal-item > .clip.clip7{
    background:url('../imgs/hover-img4.jpg');
    background-size: cover;
    clip-path: polygon(0 0, 70% 0, 30% 100%, 0 100%);
}
.instal-item > .clip.clip8{
    background:url('../imgs/hover-img8.jpg');
    background-size: cover;
    clip-path: polygon(70% 0, 100% 0, 100% 100%, 30% 100%);
