.instal-item:hover > .clips{
    clip-path: polygon(100% 0, 100% 0%, 100% 100%, 100% 100%);
}
.instal-item > .clip:hover{
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
}
.clip-content{
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
    position: absolute;
    bottom: -100%;
    opacity: 0;
    left: 0;
    width: 100%;
    padding: 10px;
    background: rgb(0, 153, 255);
    transition: .6s;
}
.clip-content > h4{
  font-size: 15px;
  font-weight: bold;
    color: #fff;
    font-style: italic;

}
.instal-item > .clip:hover .clip-content{
    bottom: 0;
    opacity: 1;
}
